
body {
  margin: 0;
  padding: 0;
  text-align: center;
}
a {
  color: #42b983;
  text-decoration: none;
}
.dplayer {
  width: 800px;
  margin: 50px auto;
}
h1 {
  font-size: 54px;
  color: #42b983;
  margin: 30px 0 10px;
}
h2 {
  font-size: 22px;
  color: #555;
}
@media (max-width: 768px) {
.dplayer {
    width: 90%;
}
h1 {
    font-size: 30px;
}
h2 {
    font-size: 16px;
}
}



